.nav .nav-link:not([class^="btn-"]):not([class*=" btn-"]) {
	font-weight: 500;
	font-size: rem($fs-base);
}

.nav-tabs .nav-link,
.nav-tabs .nav-item.show .nav-link {
	color: #333;
}

.nav-tabs .nav-link.active,
.nav-tabs .nav-item.show .nav-link.active {
	color: $primary-500;
}